.sectionCatalog{margin-top:clamp(62px,6vh,240px);margin-bottom:60px}.sectionCatalogWrapper{max-width:1200px;margin:0 auto;padding:0 10px 0 10px}.sectionCatalogWrapper .filterBlockChange{display:none;width:30%;min-width:220px;margin:60px 0 60px 0}.sectionCatalogWrapper .filterBlockChange h3{color:#a1a1a1;font-size:clamp(16px,2vw,20px);font-family:Noto Sans;font-style:normal;font-weight:400;opacity:.5;margin-bottom:20px;text-transform:uppercase}.sectionCatalogWrapper .filterBlockChange select{width:100%;padding:1%;color:#fff;font-size:clamp(16px,2vw,20px);font-family:Noto Sans;font-style:normal;font-weight:400;background-color:rgba(0,0,0,0);border:none;border-bottom:3px solid #1f9fba;outline:none}.sectionCatalogWrapper .filterBlockChange select option{color:#fff;background-color:#090e10;outline:none}.sectionCatalogWrapper .catalogTitle{font-size:clamp(46px,5.2vw,62px);font-family:Noto Sans;font-style:normal;font-weight:700}.sectionCatalogWrapper .catalogTitle h1{color:#fff}.sectionCatalogWrapper .catalogTitle h1 span{color:#1f9fba}.sectionCatalogWrapper .filterBlock{width:80%;margin:30px auto 30px auto;text-transform:uppercase;color:#fff;font-size:clamp(16px,2vw,20px);font-family:Noto Sans;font-style:normal;font-weight:400;display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center}.sectionCatalogWrapper .filterBlock .btnFilter{min-width:auto}.sectionCatalogWrapper .filterBlock .btnFilter input{width:16px;height:16px;margin-right:10px;cursor:pointer}.sectionCatalogWrapper .prodact{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;gap:18px}.sectionCatalogWrapper .prodactItem{width:31%;display:flex;flex-direction:column;align-items:center;box-shadow:0px 2px 8px 5px rgba(255,255,255,.25) inset}.sectionCatalogWrapper .prodactItem .foto{width:95%;padding:4px}.sectionCatalogWrapper .prodactItem .foto img{width:100%}.sectionCatalogWrapper .prodactItem .foto .swiper-button-prev:after,.sectionCatalogWrapper .prodactItem .foto .swiper-button-next:after{font-size:28px;font-weight:600;color:#1f9fba}.sectionCatalogWrapper .prodactItem .name,.sectionCatalogWrapper .prodactItem .prise,.sectionCatalogWrapper .prodactItem .size,.sectionCatalogWrapper .prodactItem .thickness{height:20px;padding:5px}.sectionCatalogWrapper .prodactItem .name,.sectionCatalogWrapper .prodactItem .prise{color:#1f9fba;font-size:clamp(18px,2vw,24px);font-family:Noto Sans;font-style:normal;font-weight:500}.sectionCatalogWrapper .prodactItem .prise{color:#fff}.sectionCatalogWrapper .prodactItem .prise::after{content:" â‚½"}.sectionCatalogWrapper .prodactItem .size,.sectionCatalogWrapper .prodactItem .thickness{color:#a1a1a1;font-size:clamp(16px,2vw,20px);font-family:Noto Sans;font-style:normal;font-weight:400}.btnCard{width:60%;height:50px;border-radius:10px;border:3px solid #1f9fba;background-color:#14212a;margin-top:20px;margin-bottom:20px;font-size:clamp(14px,2vw,18px);color:#a1a1a1;font-family:Noto Sans;font-style:normal;font-weight:400;transition:background-color .5s ease-in-out}.btnCard:hover{background-color:#000}@media screen and (max-width: 960px){.sectionCatalog .prodact{gap:25px}.sectionCatalog .prodactItem{width:48%}}@media screen and (max-width: 800px){.sectionCatalog{margin-top:120px}.sectionCatalogWrapper .filterBlock .btnFilter{min-width:100px}.sectionCatalogWrapper .prodactItem{width:44%}}@media screen and (max-width: 768px){.sectionCatalogWrapper .filterBlock .btnFilter{min-width:120px}}@media screen and (max-width: 640px){.sectionCatalogWrapper .filterBlock .btnFilter{min-width:170px}.sectionCatalogWrapper .prodactItem{width:100%}}